import React, { Component } from 'react';
class CalendarView extends Component{

  render(){
    return(
		<div id="CalendarView">
			<ul id="events-past"></ul>
			<ul id="events-upcoming"></ul>
		</div>
    );
  }
}

export default CalendarView;


